@import '../../variables';

.menu-item {
    &_container {
        color: $dark;
        font-family: Nunito, sans-serif;
        font-style: normal;
        padding: 5%;
        @include item-box;
    }
    &_img img{
        @include img;
        cursor: pointer;
    }
    &_price {
        @include price;
    }
    &_title {
        @include title;    
    }
    &_content {
        @include content;
        margin: 7% auto;
    }
    &_details a {
        @include details;
    }
    &_btn {
        @include btn;
        &.hidden {
            display: none;
        }
        &_viewcart {
            @include btn-viewCart;
        }
    }
}
